/*首頁選單 */		
#box_menu{
	display:block;
	position:absolute;
	bottom:39px;
	left:50%;
	width: 510px;
    margin-left: -250px}
#box_menu ul{ 
    display:block; 
    margin:0; 
    padding:0}
#box_menu li{ 
    display: inline-block; 
    list-style: none;
    background: url(../images/bg_line-white.png) right no-repeat}
#box_menu li:nth-child(3){
    background: none}
#box_menu a{
    display: block;
	height:39px;
	text-indent:100%;
    white-space: normal;
    overflow: hidden;
    font-size: 0;
	background:url(../images/btn_nav.png) no-repeat;
	margin:0 15px}
#box_menu a:nth-child(1){
    margin-right:20px}
#box_menu a.nav_01{ width: 130px; background-position: 0 0}
#box_menu a.nav_01:hover{ background-position: 0 -39px}
