    box-shadow: 0 0 35px #0c49f7;}

  50% {
    -webkit-box-shadow: 0 0 95px #0c49f7;
    -moz-box-shadow: 0 0 95px #0c49f7;
    box-shadow: 0 0 95px #0c49f7;}
 100% {
   -webkit-box-shadow: 0 0 35px #0c49f7;
    -moz-box-shadow: 0 0 35px #0c49f7;
    box-shadow: 0 0 35px #0c49f7;}}

@keyframes breathe {
 0% {
   -webkit-box-shadow: 0 0 35px #0c49f7;
    -moz-box-shadow: 0 0 35px #0c49f7;
    box-shadow: 0 0 35px #0c49f7;}

  50% {
    -webkit-box-shadow: 0 0 95px #0c49f7;
    -moz-box-shadow: 0 0 95px #0c49f7;
    box-shadow: 0 0 95px #0c49f7;}
 100% {
   -webkit-box-shadow: 0 0 35px #0c49f7;
    -moz-box-shadow: 0 0 35px #0c49f7;
    box-shadow: 0 0 35px #0c49f7;}}

.mybreathe{
   -webkit-animation-duration: 3500ms;
    -moz-animation-duration: 3500ms;
	animation-duration: 3500ms;	  
   -webkit-animation-name: breathe;
   animation-name: breathe;
   -webkit-animation-timing-function: linear;
    animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite    
}

/*
=====================================
測試站標籤
=====================================
*/
#myTag{
    position: fixed;
    top: -80px;
    right: 0;
    z-index: 990;
    border-color: transparent;
    border-left-color: RGBA(10,92,124,.8);
    border-style: solid solid solid solid;
    border-width: 50px;
    height: 300px;
    width: 50px;
    transform: rotate(-45deg);
    -ms-transform: rotate(-45deg)
    /*-webkit-box-shadow: 5px 5px 20px #8e8e8e;
    -moz-box-shadow: 5px 5px 20px #8e8e8e;
    box-shadow: 5px 5px 20px #8e8e8e;*/
}
#myTag p{
    position: absolute;
    top: 65px;
    right: -10px;
    font-size: 1.6rem;
    color: #fff;
    line-height: 22px;
    text-align: center;
    transform: rotate(91deg);
    -ms-transform: rotate(45deg)
}
    
   
						